<template>
  <div id="app">
    <transition name="router-fade" mode="out-in" >
      <keep-alive>
        <router-view />
      </keep-alive>
    </transition>
   <!--  <transition name="router-max" mode="out-in" v-if='$route.name==="chatInfo" || $route.name==="chat"'>
       <keep-alive>
        <router-view  v-if='$route.name==="chatInfo" || $route.name==="chat"'/>
      </keep-alive>
    </transition> -->
    <van-tabbar
    v-model="active"
    active-color="#08c261"
    inactive-color="#000"
    route v-show='$route.meta.isBottom'
    >
      <van-tabbar-item  to='/home'>
        <span>微信</span>
        <img
          slot="icon"
          slot-scope="props"
          :src="props.active ? message.active : message.normal"
        >
      </van-tabbar-item>
      <van-tabbar-item  to='/addressBook'>
        <span>通讯录</span>
        <img
          slot="icon"
          slot-scope="props"
          :src="props.active ? address.active : address.normal"
        >
      </van-tabbar-item>
      <van-tabbar-item  to='/find' dot>
        <span>发现</span>
        <img
          slot="icon"
          slot-scope="props"
          :src="props.active ? find.active : find.normal"
        >
      </van-tabbar-item>
      <van-tabbar-item  to='/my'>
        <span>我</span>
        <img
          slot="icon"
          slot-scope="props"
          :src="props.active ? my.active : my.normal"
        >
      </van-tabbar-item>
    </van-tabbar>
  </div>
</template>

<script>

  export default {

    name: 'App',
    data(){
      return{
          active:'home',
          message: {
            normal: require('img/images/bottomBar/message.png'),
            active:require('img/images/bottomBar/message2.png')
          },
          address:{
            normal: require('img/images/bottomBar/addressBook.png'),
            active:require('img/images/bottomBar/addressBook2.png')
          },
          find:{
            normal: require('img/images/bottomBar/find.png'),
            active:require('img/images/bottomBar/find2.png')
          },
          my:{
            normal: require('img/images/bottomBar/my.png'),
            active:require('img/images/bottomBar/my2.png')
          }
      }
    },
    methods:{

    },
    created(){
      sessionStorage.setItem('myName','枫叶回忆');
    }
  }
</script>

<style>
  #app {
    font-family: 'Avenir', Helvetica, Arial, sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    height: 100%;
    -webkit-touch-callout:none;
    -webkit-user-select:none;
    -khtml-user-select:none;
    -moz-user-select:none;
    -ms-user-select:none;
    user-select:none;
  }
  /*.router-max-enter{
      transform:translateX(1000px);
  }
  .router-max-leave-active {
     transform:translateX(-1000px) ;
  }*/
  .router-fade-enter-active, .router-fade-leave-active {
    transition:  .18s;
  }
  .router-fade-enter, .router-fade-leave-active {
    opacity: 0;
  }
  #app .van-tabbar-item__icon>img{
    width: .58rem;
    height: .58rem;
  }
  #app .van-tabbar-item__icon .van-icon {
    font-size: .4rem;
  }
  #app .van-tabbar{
    padding: .19rem 0;
    background: #f7f7f7;
  }
  .van-tabbar-item__icon--dot::after {
    background-color:#FA5251;
  }
  .van-tabbar-item__text>span{
    font-size: .3rem;
  }
   .van-tabbar-item__icon--dot::after {
    right: -.06rem !important;
  }
</style>



