import Vue from 'vue'
import Router from 'vue-router'
Vue.use(Router)

const home =()=>import("@/views/home")
const addressBook =()=>import("@/views/addressBook")
const find =()=>import("@/views/find")
const my =()=>import("@/views/my")

const chat =()=>import("@/views/chat")
const chatInfo =()=>import("@/views/chatInfo")
const userInfo =()=>import("@/views/userInfo")
const search =()=>import("@/views/search")
const myFriendsCircle =()=>import("@/views/myFriendsCircle")
const friendsCircle =()=>import("@/views/friendsCircle")
const othersCircle =()=>import("@/views/othersCircle")
const newFriends =()=>import("@/views/newFriends")
const groupChat =()=>import("@/views/groupChat")
const publicNum =()=>import("@/views/publicNum")
const label =()=>import("@/views/label")

const pay =()=>import("@/components/pay")
const myCollection =()=>import("@/components/myCollection")
const album =()=>import("@/components/album")
const set =()=>import("@/components/set")
const payManagement =()=>import("@/components/payManagement")
const myInfo =()=>import("@/components/myInfo")
export default new Router({
  routes: [

    {
      path: '/',
      redirect:'/home'
    },
    {
      path: '/home',
      name: 'home',
      component: home,
      meta:{
        isBottom:true
      }
    },
     {
      path: '/addressBook',
      name: 'addressBook',
      component: addressBook,
      meta:{
        isBottom:true
      }
    },
     {
      path: '/find',
      name: 'find',
      component: find,
      meta:{
        isBottom:true
      }
    },
    {
      path: '/my',
      name: 'my',
      component: my,
      meta:{
        isBottom:true
      }
    },
    {
      path: '/chat',
      name: 'chat',
      component: chat
    },
    {
      path: '/chatInfo',
      name: 'chatInfo',
      component: chatInfo
    },
    {
      path: '/userInfo',
      name: 'userInfo',
      component: userInfo
    },
    {
      path: '/search',
      name: 'search',
      component: search
    },
    {
      path: '/myInfo',
      name: 'myInfo',
      component: myInfo
    },
    {
      path: '/friendsCircle',
      name: 'friendsCircle',
      component: friendsCircle
    },
    {
      path: '/pay',
      name: 'pay',
      component: pay
    },
    {
      path: '/myCollection',
      name: 'myCollection',
      component: myCollection
    },
    {
      path: '/album',
      name: 'album',
      component: album
    },
    {
      path: '/payManagement',
      name: 'payManagement',
      component: payManagement
    },
    {
      path: '/set',
      name: 'set',
      component: set
    }, 
    {
      path: '/myFriendsCircle',
      name: 'myFriendsCircle',
      component: myFriendsCircle
    },
    {
      path: '/othersCircle',
      name: 'othersCircle',
      component: othersCircle
    },
    {
      path: '/newFriends',
      name: 'newFriends',
      component: newFriends
    },
    {
      path: '/groupChat',
      name: 'groupChat',
      component: groupChat
    },
    {
      path: '/label',
      name: 'label',
      component: label
    },  
    {
      path: '/publicNum',
      name: 'publicNum',
      component: publicNum
    },
    {
      path:'*',
      redirect:'/'
    }
  ]
})




